import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpRequest } from '@angular/common/http';
import { EmployeeViewModel } from 'src/Modules/Shared/ViewModels/employee';
import { Observable, throwError, of } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {

  constructor(protected http: HttpClient) { }

  private baseUrl = 'http://localhost:5000/api';

  handleError(httpError: HttpErrorResponse){
    return throwError(httpError.error);
  }

  getEmployees(): Observable<Array<EmployeeViewModel>>{
    return of<Array<EmployeeViewModel>>([
      {
        FirstName: 'Rodrigo',
        LastName: 'Bengoechea',
        SocialSecurityNumber: 1234,
        Phone: 1231241,
      },
      {
        FirstName: 'Juan',
        LastName: 'Perez',
        SocialSecurityNumber: 56789,
        Phone: 13212131
      }
    ]);
  }

  saveEmployee(employee: EmployeeViewModel): Observable<any>{
    return this.http.post(this.baseUrl + '/saveEmployee', employee).pipe(catchError(this.handleError));
  }
}
